import React, { Component } from 'react';
import {
	Row,
	Col,
	Descriptions,
	Form,
	Input,
	Select,
} from 'antd';
import img1 from "./1.png";
import img2 from "./2.png";
import img3 from "./3.png";
import 'antd/es/descriptions/style/css';
import 'antd/es/form/style/css';
import 'antd/es/input/style/css';
import 'antd/es/select/style/css';

import { fuzzyTsukamotoAlgorithm } from "./Tsukamoto";
const { Option } = Select;
const { Search } = Input;
class App extends Component {
	state = {
		jarak: 8,
		cc: 150,
		jbensin: 90,
		zTotal: '',
		alfaR1: '',
		zR1: '',
		alfaR2: '',
		zR2: '',
		alfaR3: '',
		zR3: '',
		alfaR4: '',
		zR4: '',
		alfaR5: '',
		zR5: '',
		alfaR6: '',
		zR6: ''
	};
	handleChange = e => {
		this.setState({
			[ e.target.name ]: e.target.value
		})
	}
	handleSetResult = ( data ) => {
		this.setState({
			zTotal: data.total,
			alfaR1: data.alfa[0],
			zR1: data.z[0],
			alfaR2: data.alfa[1],
			zR2: data.z[1],
			alfaR3: data.alfa[2],
			zR3: data.z[2],
			alfaR4: data.alfa[3],
			zR4: data.z[3],
			alfaR5: data.alfa[4],
			zR5: data.z[4],
			alfaR6: data.alfa[5],
			zR6: data.z[5]
		})
	}
	handleSelectChange = e => {
		this.setState({ jbensin: e })
	};
	handleSubmit = ( ) => {
		const { jarak, cc, jbensin } = this.state;
		fuzzyTsukamotoAlgorithm( jarak, cc, jbensin, this.handleSetResult )
	}
	render( ) {
		
		const {
			jarak,
			cc,
			jbensin,
			zTotal,
			alfaR1,
			alfaR2,
			alfaR3,
			alfaR4,
			alfaR5,
			alfaR6,
			zR1,
			zR2,
			zR3,
			zR4,
			zR5,
			zR6
		} = this.state;
		return (
			<div style={{
				padding: '50px'
			}}>
				<Descriptions title="Sistem Pendukung Keputusan Untuk Menentukan Jumlah Bensin Yang Harus Dibeli" layout="vertical" bordered>
					<Descriptions.Item label="Deskripsi" span={3}>
					<h2>Kelompok : </h2>
					<b>
						<ol>
							<li>(16102059) Muhammad Genta Ari Shandi </li>
							<li>(16102125) Istiana Adesty </li>
							<li>(16102158) Fikri Hermawan </li>
							<li>(16102196) Ian Aprilianto Said </li>
							<li>(16102204) Mukhlis Adhe Purwanto </li>
						</ol>
					</b>
					Dalam penggunaan sepeda motor bensin merupakan bagian penting dalam penggunaannya. Bensin adalah bahan bakar yang umum digunakan pada kendaraan bermotor yang memiliki banyak jenis dalam setiap merknya. Untuk jenis bensin pada sistem ini menggunakan jenis bensin premium, pertalite dan pertamax. Penggunaan bensin pada setiap motor berbeda beda tergantung pada kapasitas mesin, jarak, dan jenis bensin/bahan bakar. Untuk itu kami melakukan pembuatan sistem ini agar dapat mengetahui jumlah bahan bakar/ bensin yang optimal untuk digunakan pengguna sepeda motor dengan kriteria tertentu. 
					</Descriptions.Item>
					<Descriptions.Item label="Rules" span={3}>
						<Row gutter={16}>
							<Col className="gutter-row" span={8}>
								<Descriptions layout="vertical" bordered>
									<Descriptions.Item label="Rule 1" span={3}>IF jarak dekat AND Kapasitas mesin rendah AND jenis bahan bakar pertalite THEN isi bensin sedikit.</Descriptions.Item>
									<Descriptions.Item label="α-R1">{alfaR1}</Descriptions.Item>
									<Descriptions.Item label="z-R1">{zR1}</Descriptions.Item>
								</Descriptions>
							</Col>
							<Col className="gutter-row" span={8}>
								<Descriptions layout="vertical" bordered>
									<Descriptions.Item label="Rule 2" span={3}>IF jarak dekat AND kapasitas mesin sedang AND jenis bahan bakar pertalite THEN isi bensin sedikit.</Descriptions.Item>
									<Descriptions.Item label="α-R2">{alfaR2}</Descriptions.Item>
									<Descriptions.Item label="z-R2">{zR2}</Descriptions.Item>
								</Descriptions>
							</Col>
							<Col className="gutter-row" span={8}>
								<Descriptions layout="vertical" bordered>
									<Descriptions.Item label="Rule 3" span={3}>IF jarak sedang AND kapasitas mesin tinggi AND jenis bahan bakar pertamax THEN isi bensin sedikit.</Descriptions.Item>
									<Descriptions.Item label="α-R3">{alfaR3}</Descriptions.Item>
									<Descriptions.Item label="z-R3">{zR3}</Descriptions.Item>
								</Descriptions>
							</Col>
						</Row>
						<Row gutter={16} style={{
							marginTop: 20
						}}>
							<Col className="gutter-row" span={8}>
								<Descriptions layout="vertical" bordered>
									<Descriptions.Item label="Rule 4" span={3}>IF jarak jauh AND kapasitas mesin tinggi AND jenis bahan bakar premium THEN isi bensin banyak.</Descriptions.Item>
									<Descriptions.Item label="α-R4">{alfaR4}</Descriptions.Item>
									<Descriptions.Item label="z-R4">{zR4}</Descriptions.Item>
								</Descriptions>
							</Col>
							<Col className="gutter-row" span={8}>
								<Descriptions layout="vertical" bordered>
									<Descriptions.Item label="Rule 5" span={3}>IF jarak jauh AND kapasitas mesin sedang AND jenis bahan bakar premium THEN isi bensin banyak.</Descriptions.Item>
									<Descriptions.Item label="α-R5">{alfaR5}</Descriptions.Item>
									<Descriptions.Item label="z-R5">{zR5}</Descriptions.Item>
								</Descriptions>
							</Col>
							<Col className="gutter-row" span={8}>
								<Descriptions layout="vertical" bordered>
									<Descriptions.Item label="Rule 6" span={3}>IF jarak sedang AND kapasitas mesin tinggi AND jenis bahan bakar pertamax THEN isi bensin banyak.</Descriptions.Item>
									<Descriptions.Item label="α-R6">{alfaR6}</Descriptions.Item>
									<Descriptions.Item label="z-R6">{zR6}</Descriptions.Item>
								</Descriptions>
							</Col>
						</Row>
					</Descriptions.Item>
					<Descriptions.Item label="Fungsi Keanggotaan" span={3}>
					<Row style={{display:'flex', alignItems:'center', justifyContent:'center', flexDirection:'column'}}>
						<img src={img1} style={{width:'30vw', marginTop:50}}/>	
						<img src={img2} style={{width:'30vw', marginTop:50}}/>	
						<img src={img3} style={{width:'30vw', marginTop:50}}/>	
					</Row>
					</Descriptions.Item>
					<Descriptions.Item label="Input" span={3}>
						<Form layout="horizontal">
							<Form.Item label="Jarak (km)" {...{labelCol:{ span: 4 }, wrapperCol: { span: 14 }}}>
								<Input type="number" value={jarak} name="jarak" onChange={this.handleChange} placeholder="input Jarak (>0)"/>
							</Form.Item>
							<Form.Item label="CC Mesin" {...{labelCol:{ span: 4 }, wrapperCol: { span: 14 }}}>
								<Input type="number" value={cc} name="cc" onChange={this.handleChange} placeholder="input CC (>80)"/>
							</Form.Item>
							<Form.Item label="Jenis Bensin" {...{labelCol:{ span: 4 }, wrapperCol: { span: 14 }}}>
								<Select value={jbensin} style={{
									width: '100%'
								}} onChange={this.handleSelectChange}>
									<Option value={85}>Premium</Option>
									<Option value={88}>Pertalite</Option>
									<Option value={90}>Pertamax</Option>
								</Select>
							</Form.Item>
							<Form.Item label="Hasil (L)" {...{labelCol:{ span: 4 }, wrapperCol: { span: 14 }}}>
								<Search readOnly enterButton="Proses" size="large" value={zTotal} onSearch={this.handleSubmit}/>
							</Form.Item>
						</Form>
					</Descriptions.Item>
				</Descriptions>
			</div>
		)
	}
} 
export default App