const turunFunc = ( x, min, max ) => {
	return (( max - x ) / ( max - min ))
}
const naikFunc = ( x, min, max ) => {
	return (( x - min ) / ( max - min ))
}
export const jarakDekat = ( jarak ) => {
	const min = 7;
	const max = 10;
	let returnValue;
	if ( jarak > max || jarak < 1 ) {
		returnValue = 0
	} else if ( jarak < min ) {
		returnValue = 1
	} else {
		returnValue = turunFunc( jarak, min, max )
	}
	return returnValue
}
export const jarakSedang = ( jarak ) => {
	const min = 7;
	const mid = 10;
	const max = 13;
	let returnValue;
	if ( jarak < min || jarak > max ) {
		returnValue = 0
	} else if ( jarak > min && jarak < mid ) {
		returnValue = naikFunc( jarak, min, mid )
	} else if ( jarak > mid && jarak < max ) {
		returnValue = turunFunc( jarak, mid, max )
	} else {
		returnValue = 1
	}
	return returnValue
}
