export const zTotal = ( ) => {
	let a = 0;
	let b = 0;
	for ( var i = 0; i < alfa.length; i += 1 ) {
		a += (alfa[i] * z[i]);
		b += alfa[i]
	}
	console.log( 'zTotal = ', a + ' / ' + b );
	return a / b
};
export const zSedikit = ( x ) => {
	return z_sedikit_a - ( x * z_sedikit_b )
}
export const zBanyak = ( x ) => {
	return ( x * z_banyak_b ) + z_banyak_a
}